import React, { useState } from 'react';
import { withStyles } from '@material-ui/core/styles';
import { Menu, Button } from '@material-ui/core';
import { Info } from '@material-ui/icons';
import spaceTile from '../space.png';
import Images from '../components/Images';
import Overlay from '../components/Overlay';

const styles = theme => ({
    main: {
        backgroundImage: `url(${spaceTile})`,
        display: 'block',
        width: 1200 * 8,
        height: 1200 * 8,
    },
    image: {
        verticalAlign: 'middle',
        width: 1200,
        height: 1200,
    },
    noClick: {
        userSelect: 'none',
    },
});

const Main = props => {
    const { classes } = props;
    // tf.mouse is used to determine if the mouse moved during onClick
    const [tf, transform] = useState({
        initial: [0, 0],
        mouse: [0, 0],
        pos: [-2200, -2000],
        selectedTile: [139, 192],
    });
    const [selectedMap, selectMap] = useState('cogmap1');
    const [favorites, modFavorites] = useState({
        cogmap1: [
            { name: 'Telescience', location: [112, 123] },
            { name: 'AI core', location: [137, 146] },
            { name: "Captain's Locker", location: [85, 166] },
        ],
        cogmap2: [{ name: 'Telescience', location: [104, 120] }, { name: 'AI core', location: [190, 220] }],
        faintSignal: [{ name: 'RobustTec Implants', location: [266, 132] }, { name: 'Phaser', location: [285, 162] }],
        oshan: [{ name: 'AI Core', location: [196, 160] }, { name: 'Telescience', location: [181, 174] }],
        clarion: [{ name: 'AI Core', location: [133, 99] }, { name: 'Telescience', location: [153, 107] }],
    });
    const [zoom, setZoom] = useState(7);
    const scale = zoom / 10;
    // mousedown / clickDown used for wheel and mousemove events
    const [mousedown, clickDown] = useState(false);
    // focussed / focus used for keydown events.
    const [focussed, focus] = useState(false);
    // menu / doMenu used for menu events (obviously)
    const [menu, doMenu] = useState({ mouse: [0, 0], target: null });

    const iStyles = {
        divStyle: {
            transformOrigin: `0 0`,
            transform: `translate3D(${tf.pos[0]}px, ${tf.pos[1]}px, 0) scale(${scale})`,
        },
        selectorStyle: {
            zIndex: 1,
            position: 'fixed',
            ...tilePosition(tf.selectedTile),
        },
        oceanMan: {
            zIndex: 2,
            position: 'absolute',
            left: 149 * 32 * scale + tf.pos[0],
            top: 150 * 32 * scale + tf.pos[1],
            width: 32 * scale,
            height: 32 * scale,
        },
    };
    const Svg = props => (
        <svg
            onClick={props.onClick}
            onWheel={props.onWheel ? props.onWheel : mouseWheel}
            onMouseDown={props.mouseDown}
            onMouseEnter={() => focus(true)}
            onContextMenu={props.onContextMenu ? props.onContextMenu : e => e.preventDefault()}
            className={classes.noClick}
            width={32 * scale}
            height={32 * scale}
            style={props.style}
        >
            <rect
                width={`${32 * scale}px`}
                height={`${32 * scale}px`}
                stroke={props.color}
                fill="transparent"
                strokeWidth="4"
            />
        </svg>
    );
    return (
        <div className={classes.noClick} onKeyDown={e => keyDown(e)} tabIndex={0}>
            {selectedMap === 'oshan' && (
                <a
                    style={{ color: 'inherit' }}
                    href="https://www.youtube.com/watch?v=6E5m_XtCX3c"
                    rel="noopener noreferrer"
                    target="_blank"
                >
                    <Info style={iStyles.oceanMan} />
                </a>
            )}
            <Overlay
                selectedTile={tf.selectedTile}
                transform={transform}
                centerCoords={centerCoords}
                selectMap={selectMap}
                selectedMap={selectedMap}
                zoom={zoom}
                favorites={favorites}
                modFavorites={modFavorites}
            />
            <div
                onMouseLeave={() => {
                    clickDown(false);
                    return focus(false);
                }}
                onMouseEnter={() => focus(true)}
                onContextMenu={contextMenu}
                onClick={mouseClick}
                onMouseDown={mouseDown}
                onMouseUp={() => clickDown(false)}
                onMouseMove={mouseMove}
                onWheel={mouseWheel}
            >
                <div className={classes.main} style={iStyles.divStyle}>
                    <Images image={`${classes.image} ${classes.noClick}`} selectedMap={selectedMap} />
                </div>
                {favorites[selectedMap].length > 0 &&
                    favorites[selectedMap].map(fav => {
                        return (
                            <svg
                                key={`${fav.location.toString()}_SVG`}
                                onContextMenu={e => {
                                    e.stopPropagation();
                                    e.preventDefault();
                                    return modFavorites(prev => {
                                        prev[selectedMap] = prev[selectedMap].filter(items => items !== fav);
                                        return prev;
                                    });
                                }}
                                style={{
                                    zIndex: 1,
                                    position: 'fixed',
                                    ...tilePosition(fav.location),
                                }}
                                className={classes.noClick}
                                width={32 * scale}
                                height={32 * scale}
                            >
                                <rect
                                    width={`${32 * scale}px`}
                                    height={`${32 * scale}px`}
                                    stroke="#39FF14"
                                    fill="transparent"
                                    strokeWidth="4"
                                />
                            </svg>
                        );
                    })}
            </div>
            <Svg
                color="white"
                style={iStyles.selectorStyle}
                onClick={() =>
                    transform(tf => {
                        tf.selectedTile = [1, 1];
                        return tf;
                    })
                }
            />
            <Menu
                MenuListProps={{ disablePadding: true }}
                open={Boolean(menu.target)}
                anchorEl={menu.target}
                anchorPosition={{ top: menu.mouse[1], left: menu.mouse[0] }}
                anchorReference={'anchorPosition'}
                onContextMenu={e => {
                    e.preventDefault();
                    return closeMenu();
                }}
                onClose={() => closeMenu()}
            >
                <Button size="small" onClick={menuButtonClick}>
                    favorite
                </Button>
            </Menu>
        </div>
    );

    function mouseClick(e) {
        const { clientX, clientY } = e;
        // Detect if we moved
        if (tf.mouse[0] !== clientX || tf.mouse[1] !== clientY) return;
        const [imageX, imageY] = imgCoords(clientX, clientY);
        return transform(tf => {
            tf.selectedTile = tileMath(imageX, imageY);
            return tf;
        });
    }

    function mouseDown(e) {
        clickDown(true);
        const { clientX, clientY } = e;
        return transform(tf => {
            tf.mouse = [clientX, clientY];
            tf.initial = imgCoords(clientX, clientY, 1);
            return tf;
        });
    }

    function mouseMove(e) {
        if (!mousedown) return;
        const { clientX, clientY } = e;
        return transform(tf => {
            tf.pos = imgCoords(clientX, clientY, 1, tf.initial);
            return tf;
        });
    }

    function mouseWheel(e, val = 1) {
        if (mousedown) return;
        let { deltaY, clientX, clientY } = e;
        if (deltaY > 0) {
            if (zoom === 2) return;
            setZoom(zoom - val);
        } else {
            if (zoom === 15) return;
            setZoom(zoom + val);
        }
        return transform(tf => {
            const newScale = (deltaY > 0 ? zoom - val : zoom + val) / 10;
            const [imageX, imageY] = imgCoords(clientX, clientY);
            tf.pos[0] = -imageX * newScale + clientX;
            tf.pos[1] = -imageY * newScale + clientY;
            return tf;
        });
    }

    function centerCoords(centerScale = 12, modifier = [0, 0], newTile) {
        const tile = newTile ? newTile : tf.selectedTile;
        // Default to max 1.2 scale, optional arg to change this
        setZoom(centerScale);
        const newScale = centerScale / 10;
        const [tileX, tileY] = [
            (tile[0] - 1 + modifier[0]) * 32 * newScale,
            -(tile[1] - 303 + modifier[1]) * 32 * newScale,
        ];
        const [centerX, centerY] = [window.screen.width / 2, window.screen.height / 2];
        return transform(tf => {
            tf.pos[0] = -tileX + centerX;
            tf.pos[1] = -tileY + centerY;
            return tf;
        });
    }

    function keyDown(e) {
        if (!focussed) return;
        let { key } = e;
        if (key === '5' || key === ' ') return centerCoords(zoom);
        let acceptableKeys = {
            w: [0, 1],
            a: [-1, 0],
            s: [0, -1],
            d: [1, 0],
            ArrowUp: [0, 1],
            ArrowLeft: [-1, 0],
            ArrowDown: [0, -1],
            ArrowRight: [1, 0],
            '1': [-1, -1],
            '2': [0, -1],
            '3': [1, -1],
            '4': [-1, 0],
            '6': [1, 0],
            '7': [-1, 1],
            '8': [0, 1],
            '9': [1, 1],
            '+': 2,
            '-': -2,
        };
        if (!Object.keys(acceptableKeys).includes(key)) return;
        else {
            const val = acceptableKeys[key];
            if (key === '+' || key === '-') {
                if (key === '+' && zoom >= 15) return;
                if (key === '-' && zoom <= 3) return;
                setZoom(zoom + val);
                return centerCoords(zoom + val);
            }
            centerCoords(zoom, val);
            return transform(tf => {
                tf.selectedTile[0] += val[0];
                tf.selectedTile[1] += val[1];
                return tf;
            });
        }
    }

    function contextMenu(e) {
        e.preventDefault();
        const { clientX, clientY, target } = e;
        return doMenu(prev => {
            prev.target = target;
            prev.mouse = [clientX, clientY];
            return prev;
        });
    }

    function menuButtonClick(e) {
        closeMenu();
        const [imageX, imageY] = imgCoords(menu.mouse[0], menu.mouse[1]);
        return modFavorites(prev => {
            prev[selectedMap].push({
                name: `Favorite #${prev[selectedMap].length + 1}`,
                location: tileMath(imageX, imageY),
            });
            return prev;
        });
    }

    function closeMenu() {
        return doMenu(prev => {
            prev.target = null;
            return prev;
        });
    }

    function tileMath(x, y) {
        return [1 + (x - (x % 32)) / 32, 300 - (y - (y % 32)) / 32];
    }

    function tilePosition(x, y) {
        if (!y && typeof x === 'object') [x, y] = x;
        return {
            left: (x - 1) * 32 * scale + tf.pos[0],
            top: -(y - 300) * 32 * scale + tf.pos[1],
        };
    }

    function imgCoords(x, y, funcScale = scale, pos = tf.pos) {
        return [x - pos[0], y - pos[1]].map(i => i / funcScale);
    }
};

export default withStyles(styles)(Main);
